@media (max-width: 991px) {
  .social-media-share-container {
    display: flex;
    border: solid var(--color-gray) 1px;
    border-radius: 10px;
    overflow: hidden;
  }
  .social-media-share-container a {
    width: 100%;
    height: auto;
    overflow: hidden;
    padding: 20px 0;
    justify-content: center;
    text-align: center;
  }
  .social-media-share-container a:hover {
    background: var(--color-gray-light-2);
  }
  .social-media-share-container a + a {
    border-left: 1px solid var(--color-gray);
  }
}
@media (min-width: 992px) {
  .block-iccwc-social-media-share {
    width: 120px;
    margin-left: auto;
    position: sticky;
    top: 20px;
  }
  .toolbar-fixed .block-iccwc-social-media-share {
    top: 90px;
  }
  .social-media-share-container {
    width: 120px;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 0;
    border: solid var(--color-gray) 1px;
    border-radius: 10px;
    overflow: hidden;
  }
  .social-media-share-container a {
    width: 100%;
    height: auto;
    overflow: hidden;
    padding: 20px 45px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .social-media-share-container a:hover {
    background: var(--color-gray-light-2);
  }
  .social-media-share-container a:not(:first-child) {
    border-top: 1px solid var(--color-gray);
  }
}